import React from "react"

const Reviews = () => {
  const selectReview = e => {
    document.querySelector(".selected").classList.remove("selected")
    document
      .querySelectorAll(".review")
      .forEach(review => review.classList.add("d-none"))
    document
      .querySelector(
        `.review:nth-child(${e.target.getAttribute("data-review-num")})`
      )
      .classList.remove("d-none")
    e.target.classList.add("selected")
  }
  return (
    <section id="reviews">
      <div className=" py-5 container">
        <div className="row">
          <div className=" mb-5 review-selector col-12 d-flex justify-content-center">
            <div
              onClick={selectReview}
              data-review-num="1"
              className="mx-2 selected"
            ></div>
            <div
              onClick={selectReview}
              data-review-num="2"
              className="mx-2"
            ></div>
            <div
              onClick={selectReview}
              data-review-num="3"
              className="mx-2"
            ></div>
          </div>
          <div className="col-12 text-center">
            <div className="review">
              <p className="author text-uppercase">Forbes Magazine</p>
              <p className="quote">
                "If you had room for just one grill, look no further than this
                masterpice!"
              </p>
            </div>
            <div className="review d-none">
              <p className="author text-uppercase">John Smith</p>
              <p className="quote">
                "Lorem ipsum dolor sit amet consectetur adipisicing elit. Sequi
                corporis ratione saepe cupiditate esse quibusdam."
              </p>
            </div>
            <div className="review d-none">
              <p className="author text-uppercase">Jane Doe</p>
              <p className="quote">
                "Lorem ipsum dolor, sit amet consectetur adipisicing elit.
                Sapiente asperiores similique facere illo aliquam porro
                veritatis veniam quos iure corrupti?"
              </p>
            </div>
            <a
              href="/"
              className="px-3 py-2 d-inline-block press-link text-uppercase"
            >
              Read More Press
            </a>
          </div>
        </div>
      </div>
    </section>
  )
}

export default Reviews
